<template>
	<div class="header">
		<!-- 导航栏 -->
		<el-menu class="el-menu-demo" mode="horizontal" 
		background-color="black" text-color="white" active-text-color="white">
			<el-menu-item index="home" style="font-size: 15px;" @click="toHome">
				首页
			</el-menu-item>
			<el-menu-item index="ranking" style="font-size: 15px;" @click="toRank">
				排行榜
			</el-menu-item>
			<el-menu-item index="songSheet" style="font-size: 15px;" @click="toSheet">
				分类歌单
			</el-menu-item>
			<el-menu-item index="singer" style="font-size: 15px;" @click="toSinger">
				歌手
			</el-menu-item>
			<el-menu-item index="newDisc" style="font-size: 15px;" @click="toAlbum">
				新碟上架
			</el-menu-item>
			<el-input v-model="keywords" prefix-icon="el-icon-search" @keyup.enter.native="searchMusic()" placeholder="歌手/歌曲/专辑..."></el-input>
			<span v-if="phone==''">
				<el-button round @click="login">登录</el-button>
				<span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
			</span>
			<span v-if="phone!=''">
				<el-dropdown >
				  <i class="el-icon-edit" style="color: aliceblue; font-size: 20px;margin-top: 23px; margin-right: 15px"></i>
				  <el-dropdown-menu slot="dropdown">
				    <el-dropdown-item >当前用户：{{phone}}</el-dropdown-item>
					<el-button type="danger" round style="margin-left: 20px;" @click="loginOut()">退出登录</el-button>
				  </el-dropdown-menu>
				</el-dropdown>
			</span>
			
		</el-menu>
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
		data(){
			return{
				//搜索的词
				keywords:"",
				//歌曲信息
				musicList:[],
				phone:this.$store.state.phone,
				
			}
		},
		methods: {
			//获取歌曲
			searchMusic(){
				var that=this;
				//alert(this.keywords)
				axios.get("https://autumnfish.cn/search?keywords="+this.keywords)
				.then((response)=>{
					that.musicList=response.data.result.songs;
					this.$store.commit("addMuLis",that.musicList);//存值
				},(err)=>{})			
				this.$router.push({
					name:"navigation",
				})
			}, 
			toRank(){
				this.$router.push({
					name:"ranking",
				})
			},
			toHome(){
				this.$router.push({
					name:"musicHome",
				})
			},
			toSheet(){
				this.$router.push({
					name:"hotPage",
				})
			},
			toAlbum(){
				this.$router.push({
					name:"album",
				})
			},
			toSinger(){
				this.$router.push({
					name:"singertag",
				})
			},
			//登录
			login() {
				this.$router.push({
					name: "login",
				});
			},
			loginOut(){
				this.$router.go(0);
			},
			
		}
	}
</script>

<style scoped>
.el-menu-demo{
	display: flex;
	justify-content: center;
	height: 70px;
}
.el-menu--horizontal>.el-menu-item {
    float: left;
	height: 70px;
    line-height: 70px;
}
.el-input{
	width: auto;
	margin: auto 20px;
}
a{
	text-decoration: none;
}
.el-button.is-round {
    border-radius: 20px;
    padding: 12px 23px;
    height: 35px;
    margin-top: 16px;
}
</style>
